@import "default";

.pagination2 {
	text-align: center;
	padding: 20px 0;
	a:link,
	a:visited {
		color: $textColor; }
	a:hover,
	a:active {
		color: rgba($textColor, 0.8); }
	>li {
		display: inline; }
	>a {
		width: 26px;
		height: 30px;
		display: inline-block;
		text-align: center;
		margin-left: 3px;
		background: $pageBg;
		font-size: 16px;
		line-height: 30px;
		overflow: hidden;
		&:first-child {
			margin-left: 0; }
		&.active {
			background: $pageActiveBg;
			color: $pageHoverColor; }
		&.disabled {
			color: $pageDisabled; }
		&:hover {
			&:not([class*="disabled"]) {
				background: $pageHoverBg;
				color: $pageHoverColor; } } } }
